<template>
  <div>
    <search-bar @onSearch="handleSerch"></search-bar>
      <div class="content">
          <el-menu
                  class="categories"
                  :default-active="activeIndex"
                  @select="handleSelect"
                  active-text-color="red">
              <el-menu-item index="0"  >
                  <i class="el-icon-menu"></i>
                  <span slot="title">全部</span>
              </el-menu-item>
              <el-menu-item :index="item.id" v-for="item in menuItem" :key="item.title">
                  <i class="el-icon-menu"></i>
                  <span slot="title">{{item.title}}</span>
              </el-menu-item>
          </el-menu>
          <div class="movie-list">
              <el-row>
                  <el-col :span="6" v-for="(item, index) in movieList" :key="item.id" >
                      <router-link :to="`/movieDetail/${item.id}`">
                          <el-card :body-style="{ padding: '0px' }">
                              <img :src="item.url" class="image">
                              <div style="padding: 0px 8px 15px 8px;">
                                  <div style="font-size: 18px;margin-bottom:10px">{{item.title}}</div>
                                  <div class="content-detail" >{{item.content}}</div>
                              </div>
                          </el-card>

                      </router-link>

                  </el-col>
              </el-row>
          </div>
      </div>
  </div>
</template>
<script>

  import SearchBar from './SearchBar'
  import bg2 from '../../assets/bg2.jpg'
  const axios = require('axios')
  export default {
    components: {SearchBar},
    data () {
      return {
        activeIndex:'0',
        active:'0',
        menuItem: [{
          id: '1',
          title: '动漫'
        },{
          id: '2',
          title: '电影'
        },{
          id: '3',
          title: '电视剧'
        },
        ],
        movieList:[],
      }
    },
    created (){
      var storage=window.localStorage;
      //写入a字段
      const movieList = [
        {
          menuId: '1',
          id: '1',
          title: '动漫1',
          content: '动漫1111111111111111111111111111',
          url: bg2,
          videoSrc: 'https://media.w3.org/2010/05/sintel/trailer_hd.mp4',
        },{
          menuId: '1',
          id: '2',
          title: '动漫2',
          content: '动漫2222222222222222',
          url: 'https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png',
          videoSrc: 'https://media.w3.org/2010/05/sintel/trailer_hd.mp4'
        },{
          menuId: '1',
          id: '3',
          title: '动漫3',
          content: '动漫33333333',
          url: 'https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png',
          videoSrc: 'https://media.w3.org/2010/05/sintel/trailer_hd.mp4'
        },{
          menuId: '2',
          id: '4',
           title: '电影3',
          content: '电影33333333',
          url: 'https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png',
          videoSrc: 'https://media.w3.org/2010/05/sintel/trailer_hd.mp4'
        },{
          menuId: '2',
          id: '5',
          title: '电影4',
          content: '电影33333333',
          url: 'https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png',
          videoSrc: 'https://media.w3.org/2010/05/sintel/trailer_hd.mp4'
        },{
          menuId: '3',
          id: '6',
          title: '电视剧3',
          content: '电视剧33333333',
          url: 'https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png',
          videoSrc: 'https://media.w3.org/2010/05/sintel/trailer_hd.mp4'
        },{
          menuId: '3',
          id: '7',
          title: '电视剧4',
          content: '电视剧33333333',
          url: 'https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png',
          videoSrc: 'https://media.w3.org/2010/05/sintel/trailer_hd.mp4'
        }


      ];
      storage.setItem('movieList',JSON.stringify(movieList));

      this.movieList = JSON.parse(storage.getItem('movieList'));
    },
    methods: {
      handleSelect (key) {
        console.log(key);
        this.active = key;
        const movieList = JSON.parse(window.localStorage.getItem('movieList'));
        if (key == 0) {
          this.movieList = movieList;
        } else {
          this.movieList = movieList.filter((item) => {
            return item.menuId === key
          })
        } // this.$emit('indexSelect')
      },

      //处理搜索函数
      handleSerch (keywords) {
        //连接服务器进行通信
        axios({
          method: 'post',
          url: '/index?search='+keywords,
          header: {
            // 如果写成contentType会报错
            'Content-Type': 'application/json'
          }
        }).then(resp => {
          //接收服务端返回的数据，都是数组
          let authors = resp.data.author
          let videos = resp.data.video
        }).catch(failResponse => {})
      }
    }
  }
</script>

<style>
  .nav-menu {
    /*margin-bottom: 40px;*/
    box-shadow: 0 2px 4px 8px rgba(0,0,0,.05);
  }
  .content{
      width: 1200px;
      margin: 30px auto;
      position: relative;
  }
  .categories {
      position: fixed;
      top: 123px;
      width: 150px;
  }
    .movie-list{
        margin-left: 180px;
    }
    .movie-list a{
        color: #999;
        text-decoration: none;
    }
  .el-card{
      background: transparent;
      padding-top: 10px;

  }
  .image{
      width: 235px;
      height: 235px;
  }
    .content-detail{
        text-align: left;word-break: break-all;

        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
        color: #999;

    }
</style>



